import React, { useState } from "react";
import classes from "./Input.module.css";

let id = 6;

function Input(props) {
  const [addInput, setAddInput] = useState("");

  const handleAddTodo = (e) => {
    e.preventDefault();
    if (!addInput)
      return window.confirm(
        "Oops! You didn't specify a to-do. Please enter your next task to add on to your list."
      );
    const todo = {
      id: id,
      description: addInput,
      completed: false,
    };
    props.onHandleAddToList(todo);
    setAddInput("");
    id++;
  };

  return (
    <section className={classes.Input}>
      <form onSubmit={handleAddTodo} autoComplete="off">
        <div className={classes.Flex}>
          <span className={classes.Circle} onClick={handleAddTodo}></span>
          <input
            type="text"
            name="add"
            id="add"
            placeholder="Enter your new task..."
            value={addInput}
            onChange={(e) => setAddInput(e.target.value)}
          />
        </div>
      </form>
    </section>
  );
}

export default Input;
